import React from 'react';
import { Box, Heading, Image, Grid } from '@chakra-ui/react';

const GithubStatistics = () => {
    return (
        <Box>
            <Heading
                style={{
                    display: 'flex',
                    justifyContent: 'center',
                    fontSize: '30px',
                }}
                mb={4}
                id="github-statics-title"
                className="github-stats-title" // Added class for the title
            >
                My Statistics
            </Heading>
            <Box
                margin="auto"
                display='flex' // Responsive display style
                flexDirection={{ base: 'column', md: 'row' }} // Responsive flexDirection
                justifyContent="space-around"
                alignItems="center"
                padding="20px"
                id="statics"
                className="github-stats-container" // Added class for the container
            >
                {/* Your other GitHub statistics images */}
                <Grid
                    templateColumns={{
                        base: '1fr',  // 1 column on small screens
                        md: '1fr 1fr',  // 2 columns in the middle for medium screens
                        lg: '1fr 1fr 1fr',  // 3 columns on large screens
                    }}
                    gap={4}
                >
                    <Image
                        src="https://github-readme-streak-stats.herokuapp.com/?user=Deepak1095"
                        alt="GitHub Streak"
                        mb={4}
                        id="github-streak-stats" // Added ID for GitHub Streak
                        className="github-stats-image" // Added class for the image
                    />
                    <Image
                        src="https://github-readme-stats.vercel.app/api?username=Deepak1095"
                        alt="GitHub Stats"
                        mb={4}
                        id="github-stats-card" // Added ID for GitHub Stats
                        className="github-stats-image" // Added class for the image
                    />
                    <Image
                        src="https://github-readme-stats.vercel.app/api/top-langs?username=Deepak1095"
                        alt="Top Languages"
                        id="github-top-langs" // Added ID for Top Languages
                        className="github-stats-image" // Added class for the image
                    />
                </Grid>
            </Box>
            <Box
                className="react-activity-calendar"
                style={{ width: '100%', textAlign: 'center', padding: '20px' }}
            >
                {/* Replace with your GitHub calendar content */}
                <Image
                    className="github-calendar"
                    src="https://ghchart.rshah.org/Deepak1095"
                    alt="GitHub Calendar"
                    width={{ base: "100%", md: "85%", lg:"70%" }} // Use 100% width for responsiveness
                    margin={"auto"}
                    height="auto" // Maintain aspect ratio for responsiveness
                />
            </Box>
        </Box>
    );
};

export default GithubStatistics;
