import React from "react";
import { ChakraProvider, Box, chakra, Text, Heading, Button, Link, Flex, Image } from "@chakra-ui/react";
import Typewriter from "typewriter-effect";
import { Link as RouterLink } from "react-router-dom";
import personalPhoto from "./photos/personalPhoto.jpg";
const Home = () => {
    const animatedText = {
        first: "Generative AI Enthusiast",
        second: "Full Stack Web Developer",
        third: "MERN Stack Expert",
    };

    const introDescription = "Full Stack Web Developer proficient in MERN stack and Generative AI, with strong problem-solving skills and adaptability for collaborative projects.";

    const handleResumeDownload = () => {
        const downloadLink = document.createElement('a');
        downloadLink.href = 'https://drive.google.com/file/d/1DfOFeURD7CgoBOgMP3l2uLD19OYEqL3Z/view?usp=sharing';
        downloadLink.target = '_blank';
        downloadLink.download = 'resume.pdf';
        downloadLink.click();
        setTimeout(() => {
            window.location.href = 'https://drive.google.com/uc?export=download&id=1DfOFeURD7CgoBOgMP3l2uLD19OYEqL3Z';
        }, 0);
    };

    return (
        <ChakraProvider>
            <section id="home" className="home section">
                <Flex
                    flexDirection={{ base: "column", lg: "row" }}
                    height={{ base: "auto", lg: "calc(100vh - 60px)" }}
                    marginTop="60px"
                    alignItems="center"
                    justifyContent="center"
                >
                    <Box width={{ base: "100%", lg: "40%" }} textAlign="center" padding={"5"} marginBottom={{ base: "4", lg: "0" }}>
                        <Box>
                            <Heading as="h2" mb="1rem" style={{ fontSize: "30px", fontWeight: "bold" }} id="user-detail-name">
                                Hey I’m Deepak Dhankhar👋
                            </Heading>
                            <Heading as="h1" className="fluidz-48" mb="1rem" style={{ fontSize: "28px" }}>
                                <Typewriter
                                    options={{
                                        strings: [animatedText.first, animatedText.second, animatedText.third],
                                        autoStart: true,
                                        loop: true,
                                        deleteSpeed: 10,
                                    }}
                                />
                            </Heading>
                            <Text mb="1rem" style={{ fontSize: "18px", color: "gray" }}>
                                {introDescription}
                            </Text>
                            <Box pb="5" id="resume-button-2" className="resume-button">
                                <Link id="resume-button-2" onClick={handleResumeDownload} style={{ textDecoration: "none", marginRight: "10px" }} backgroundColor="blue.500"
                                    color="white"
                                    paddingX="16px"
                                    paddingY="8px"
                                    margin="16px"
                                    fontSize="16px"
                                    fontWeight="bold"
                                    _hover={{ backgroundColor: "blue.300", color: "white" }}
                                    >
                                    Resume
                                </Link>
                            </Box>
                            <Box display="flex" justifyContent="center" mt={4}>
                                <Link
                                    href="https://github.com/Deepak1095"
                                    target="_blank"
                                    _hover={{ backgroundColor: '#367280', border: '1px solid black'}}
                                    border= '1px solid #145091' 
                                    mx={4}
                                    id="contact-github"
                                    className="contact-link"
                                >
                                    <chakra.img
                                        id="contact-github-img"
                                        src="https://cdn-icons-png.flaticon.com/512/25/25231.png"
                                        alt=""
                                        w={12}
                                        h={12}
                                        borderRadius="10px"
                                        bg="white"
                                        p={1}
                                    />
                                </Link>
                                <Link
                                    href="https://www.linkedin.com/in/deepak-dhankhar-1884b8244/"
                                    target="_blank"
                                    _hover={{ backgroundColor: '#367280', border: '1px solid black'}}
                                    border= '1px solid #145091' 
                                    mx={4}
                                    id="contact-linkedin"
                                    className="contact-link"
                                >
                                    <chakra.img
                                        id="contact-linkedin-img"
                                        src="https://static.vecteezy.com/system/resources/previews/018/930/587/original/linkedin-logo-linkedin-icon-transparent-free-png.png"
                                        alt=""
                                        w={12}
                                        h={12}
                                        borderRadius="10px"
                                        bg="white"
                                        p={1}
                                    />
                                </Link>
                            </Box>
                        </Box>
                    </Box>
                    <Box
                        width={{ base: "100%", lg: "40%" }}
                        overflow="hidden"
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                    >
                        <Image
                            src={personalPhoto}
                            alt="Deepak Dhankhar"
                            width={{ base: "45%", lg: "55%" }}
                            height="auto"
                            objectFit="cover"
                            borderRadius="45%"
                            className="home-img"
                        />
                    </Box>
                </Flex>
            </section>
        </ChakraProvider>
    );
};

export default Home;
