import React from 'react';
import {
  Box,
  Text,
  Link,
  chakra,
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverArrow,
  PopoverCloseButton,
  PopoverHeader,
  PopoverBody,
} from '@chakra-ui/react';

const Contact = () => {
  return (
    <Box id="contact" className="contact section" bg="#1b6ec4" color="white" p={{ base: 4, md: 8 }}>
      <Text id="contact-title" className="contact-title" fontSize={{ base: '4xl', md: '3xl' }} fontWeight="bold" textAlign="center">
        Contact
      </Text>
      <Text id="contact-phone" className="contact-phone" fontSize={{ base: 'lg', md: 'xl' }} textAlign="center">
        Contact Number - 7056574616
      </Text>
      <Popover>
        <PopoverTrigger id="contact-email">
          <Link
            id="contact-email"
            href="mailto:ddhankhar@gmail.com"
            display="block"
            color="white"
            mt={2}
            textAlign="center"
            textDecoration="none"
            fontSize={{ base: 'lg', md: 'xl' }}
            _hover={{ color: 'rgb(226, 201, 201)' }}
          >
            Email Address - ddhankhar@gmail
          </Link>
        </PopoverTrigger>
        <PopoverContent>
          <PopoverArrow />
          <PopoverCloseButton />
          <PopoverHeader>Email Address</PopoverHeader>
          <PopoverBody id="contact-email">ddhankhar@gmail.com</PopoverBody>
        </PopoverContent>
      </Popover>
      <Text id="contact-address" className="contact-address" fontSize={{ base: 'lg', md: 'xl' }} textAlign="center">
        Address - Bahadurgarh, Haryana
      </Text>
      <Box id="social-account" className="social-account" display="flex" justifyContent="center" mt={4}>
        <Link
          href="https://github.com/Deepak1095"
          target="_blank"
          _hover={{ backgroundColor: '#a9d0d9', border: '1px solid #145091' }}
          mx={4}
          className="contact-github-link"
        >
          <chakra.img
            id="contact-github-img"
            src="https://cdn-icons-png.flaticon.com/512/25/25231.png"
            alt=""
            w={12}
            h={12}
            borderRadius="10px"
            bg="white"
            p={1}
          />
        </Link>
        <Link
          href="https://www.linkedin.com/in/deepak-dhankhar-1884b8244/"
          target="_blank"
          _hover={{ backgroundColor: '#a9d0d9', border: '1px solid #145091' }}
          mx={4}
          className="contact-linkedin-link"
        >
          <chakra.img
            id="contact-linkedin-img"
            src="https://static.vecteezy.com/system/resources/previews/018/930/587/original/linkedin-logo-linkedin-icon-transparent-free-png.png"
            alt=""
            w={12}
            h={12}
            borderRadius="10px"
            bg="white"
            p={1}
          />
        </Link>
      </Box>
    </Box>
  );
};

export default Contact;
