import React from 'react';
import { Box, Heading, Text, Link } from '@chakra-ui/react';

function About() {
    return (
        <Box className="about section" id="about" p={{ base: '10px', md: '20px' }} display="flex" flexDirection="column" alignItems="center" width="100%" marginTop={"50px"} >
            <Box textAlign="center" width={{ base: '90%', md: '90%', lg: "60%" }} margin={"auto"} >
                <Heading as="h2" fontSize={{ base: '16px', md: '30px' }} marginBottom="10px" className="about-title">
                    About me
                </Heading>
                <Text id="user-detail-intro" className="about-description" fontSize="16px" lineHeight={1.6}>
                I'm a MERN stack developer with a flair for integrating Generative AI into projects. Proficient in HTML, CSS, JavaScript, React, Redux, MongoDB, Express, Node.js, and Chakra UI, I'm passionate about crafting user-centric web experiences. My adaptability, analytical thinking, and collaborative spirit make me a valuable asset to dynamic teams. Seeking opportunities with forward-looking organizations to contribute my skills and creativity.
                </Text>
            </Box>
        </Box>
    );
}

export default About;
