import React, { useEffect, useState } from 'react';
import {
    Box,
    Text,
    Heading,
    Image,
    Link,
    VStack,
    HStack,
    useBreakpointValue
} from '@chakra-ui/react';

const projects = [
    {
        imageSrc: ["https://user-images.githubusercontent.com/111503473/229414367-a266080c-173b-4ba8-bf13-ad2014e740a9.png", "https://user-images.githubusercontent.com/111503473/229414517-a4dfdcb1-14a7-4e02-8049-eb11d1ef194f.png", "https://github.com/Deepak1095/1mg-clone/assets/111503473/b6d1c021-13fd-45d6-b2a2-ce7fd09643d7", "https://github.com/Deepak1095/1mg-clone/assets/111503473/2b9219e4-93da-4dca-99b6-d8a892e7b3fe"],
        title: "Tata 1mg",
        description: "Tata 1mg is an online healthcare platform that offers a wide range of services related to medicine and healthcare.",
        githubLink: "https://github.com/Deepak1095/1mg-Clone",
        liveLink: "https://subtle-buttercream-439a56.netlify.app/",
    },
    {
        imageSrc: ["https://github.com/Deepak1095/humorous-machine-6627/assets/111503473/7b844e87-22f2-4211-b36a-d3cb8cb8cedb", "https://github.com/Deepak1095/Beauty_Bebo/assets/111503473/96ec7623-7b49-49e0-a53b-ff0a57337849", "https://github.com/Deepak1095/Beauty_Bebo/assets/111503473/ba3ffe6b-acc0-4045-a94f-3e726a0f7fd5", "https://github.com/Deepak1095/Beauty_Bebo/assets/111503473/d8a76a44-a15e-45ec-b92c-5c08b9d16c11"],
        title: "Beauty Bebo",
        description: "Buy Best Cosmetics & Beauty Products Online From Beauty Bebo.",
        githubLink: "https://github.com/Deepak1095/humorous-machine-6627",
        liveLink: "https://beautybebo-blond.vercel.app/",
    },
    {
        imageSrc: ["https://github.com/Deepak1095/Quick-Loan/assets/111503473/b1d46961-a523-4974-9f01-350962786925", "https://github.com/Deepak1095/Quick-Loan/assets/111503473/14570a6e-b778-435e-9193-1940a175394f", "https://github.com/Deepak1095/Quick-Loan/assets/111503473/4aac7e29-b958-4d0a-a5c1-75b48c3ea6ec", "https://github.com/Deepak1095/Quick-Loan/assets/111503473/56aa083b-f81c-448e-8056-e2cf5852f971"],
        title: "Loan Distribution",
        description: "Our user-friendly loan distribution website makes borrowing effortless, connecting borrowers with lenders for quick and easy access to funds.",
        githubLink: "https://github.com/Deepak1095/humorous-machine-6627",
        liveLink: "https://astounding-gumdrop-adeb4b.netlify.app/",
    },
    {
        imageSrc: ["https://github.com/lokendra0905/loud-weight-1875/assets/111503473/4a2bc796-cef3-45d0-b261-8a427f02c0f5", "https://github.com/lokendra0905/loud-weight-1875/assets/111503473/4a2bc796-cef3-45d0-b261-8a427f02c0f5", "https://github.com/lokendra0905/loud-weight-1875/assets/111503473/4a2bc796-cef3-45d0-b261-8a427f02c0f5", "https://github.com/lokendra0905/loud-weight-1875/assets/111503473/4a2bc796-cef3-45d0-b261-8a427f02c0f5",],
        title: "PetConnects",
        description: "Welcome to Our Pet Shelter and Adoption Website.",
        githubLink: "https://github.com/Deepak1095/PetConnects",
        liveLink: "https://petconnects.vercel.app/",
    },
];

const Projects = () => {
    const [currentImageIndex, setCurrentImageIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentImageIndex((prevIndex) => (prevIndex === projects[0].imageSrc.length - 1 ? 0 : prevIndex + 1));
        }, 2000);

        return () => {
            clearInterval(interval);
        };
    }, [currentImageIndex]);
    const flexDirection = useBreakpointValue({ base: 'column', md: 'row' });

    return (
        <Box id="projects" className="projects section" paddingTop="50px">
            <Heading id="project-heading" className="project-heading" mb="4" display="flex" justifyContent="center" fontSize="30px">
                Projects
            </Heading>
            <VStack spacing="4" align="stretch" className="project-card-container card" width={{ base: "100%", md: "95%", lg: "70%" }} margin="auto" mb="50px">
                {projects.map((project, index) => (
                    <Box
                        key={index}
                        alignItems="center"
                        padding="25px"
                        margin="20px"
                        border="1px solid teal"
                        display="flex"
                        className="project-card"
                        flexDirection={flexDirection === 'column' ? 'column' : index % 2 === 0 ? 'row' : 'row-reverse'}
                    >
                        <Box flex="1">
                            <Image src={project.imageSrc[currentImageIndex]} alt={`Project Image ${currentImageIndex}`} width="100%" height="250px" />
                        </Box>
                        <Box flex="1" padding="20px">
                            <Text fontWeight="bold" fontSize="lg" mb="2" className='project-title'>
                                {project.title}
                            </Text>
                            <Text classname="project-description">{project.description}</Text>
                            <HStack spacing="2" mt="2" display={"flex"} justifyContent={"center"} paddingTop={"5px"}>
                                <Link href={project.liveLink} target="_blank" rel="noopener noreferrer" textDecoration="none" color="rgb(0, 0, 0)" padding="7px" border="3px solid teal" borderRadius="5px" fontSize="large" className="project-deployed-link"> 
                                    Go Live
                                </Link>
                                <Link href={project.githubLink} target="_blank" rel="noopener noreferrer" textDecoration="none" color="rgb(0, 0, 0)" padding="7px" border="3px solid teal" borderRadius="5px" fontSize="large" className="project-github-link"> 
                                    Git Code
                                </Link>
                            </HStack>
                        </Box>

                    </Box>
                ))}
            </VStack>
        </Box>
    );
};

export default Projects;
