import React from 'react';
import { Box, Heading, Flex, Image } from '@chakra-ui/react';

const TechStack = () => {
    const techSkills = [
        {
            name: 'JavaScript',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/512px-Unofficial_JavaScript_logo_2.svg.png',
        },
        {
            name: 'HTML',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/512px-HTML5_logo_and_wordmark.svg.png',
        },
        {
            name: 'CSS',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/3d/CSS.3.svg/512px-CSS.3.svg.png',
        },
        {
            name: 'React',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png',
        },
        {
            name: 'Node.js',
            imageSrc: 'https://nodejs.org/static/images/logo-light.svg',
        },
        {
            name: 'MongoDB',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/93/MongoDB_Logo.svg/512px-MongoDB_Logo.svg.png',
        },
        {
            name: 'Postman',
            imageSrc: 'https://assets.getpostman.com/common-share/postman-logo-horizontal-white.svg',
        },
        // Add more skills here
    ];

    return (
        <Box as="section" id="skills" className="skills section" p={{ base: 4, md: 6, lg: 8 }} marginBottom={{ base: "100px", md: "20px" }} width={{ base: "90%", md: "90%", lg: "70%" }}
            borderWidth={3} m="auto">
            <Heading as="h2" size="xl" mb={4} textAlign="center" paddingBottom="20px" marginTop={{ base: "0px", md: "20px" }}>
                Tech Stack
            </Heading>
            <Box
                display="grid"
                width="100%"
                className="skills-grid" // Added class for the grid container
                gridTemplateColumns={{
                    base: 'repeat(2, 1fr)',
                    md: 'repeat(4, 1fr)',
                    lg: 'repeat(4, 1fr)'
                }}
                gap={6}
                margin="auto"
            >
                {techSkills.map((skill) => (
                    <Box
                        key={skill.name}
                        p={4}
                        textAlign="center"
                        borderWidth={3}
                        borderRadius="md"
                        bg="white"
                        transition="transform 0.2s"
                        _hover={{ transform: 'scale(1.05)' }}
                        h="200px"
                        display="flex"
                        flexDirection="column"
                        justifyContent="center"
                        alignItems="center"
                        className="skills-card" // Added class for each skill card
                    >
                        <Image
                            src={skill.imageSrc}
                            alt={skill.name}
                            maxW="80px"
                            mx="auto"
                            className="skills-card-img" // Added class for the image
                        />
                        <Heading as="h2" size="md" mt={2} className="skills-card-name"> {/* Added class for the skill name */}
                            {skill.name}
                        </Heading>
                    </Box>
                ))}
            </Box>
        </Box>
    );
};

export default TechStack;
